import React from 'react';
import styled from 'react-emotion';
import { Field, reduxForm } from 'redux-form'
// import PropTypes from 'prop-types';
import submit from './submit'

const Wrapper = styled.div`
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
`;

const FormWrapper = styled.div`
	text-align: center;
	padding: 0.5rem;
	margin-bottom: 0;
`;

const Nav = styled.div`
	text-align: center;
	color: white;
`;

const AppName = styled.h1`
	font-size: calc(5vw + 5vh);
`;

const Appeal = styled.h2`
	padding: 0 2rem;
`;

const Logo = styled.h1`
`;

const Cta = styled.h3`
	color: white;
	margin-bottom: 1rem;
`;

const Form = styled.form`
	margin: 0.5rem;
	padding: 0.5rem;
	cursor: pointer;
	display: flex;
	justify-content: center;
	
	> * {
		&:first-child {
		}
	}
	`;

const Email = styled(Field)`
	padding: 1.5rem 2rem;
	margin-right: 0;
	font-size: 1.5rem;
	line-height: 1.27777778em;
	`;

const Submit = styled.button`
	color: #fff;
	background: rgba(150, 200, 130, 1);
	padding: 1rem;
	font-size: 1.5rem;
	line-height: 1.27777778em;
	
	border: none;
	outline: none;
	cursor: pointer;
`;

const Landing = ({ handleSubmit, submitting}) => (
	<Wrapper>
		<Nav>
			<AppName>youme.world</AppName>
			<Appeal>Trip planning for busy wanderers.</Appeal>
		</Nav>
		<Logo role="img" aria-role="img">
			🌍📌
		</Logo>
		<FormWrapper>
			<Cta>Earn passive income traveling and blogging!</Cta>
			<Form onSubmit={handleSubmit(submit)}>
				<Email name="email" component="input" type="text" placeholder="wander@youme.world"/>
				<Submit type="submit" disabled={submitting}>Intersted</Submit>
			</Form>
		</FormWrapper>
	</Wrapper>
);

// Landing.propTypes = {};

export default reduxForm({
  form: 'contact'
})(Landing);



